html, body {
    padding: 0;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.auth {
    display: -webkit-flex;
    display: flex;
    padding-top: 50px;
    -webkit-justify-content: center;
            justify-content: center;
}

.auth .card {
    width: 400px;
}


.chat {
    display: -webkit-flex;
    display: flex;
    height: 70vh;
    width: 100%;
}



.chat-users {
    width: 300px;
    border: 1px solid #e0e0e0;
    margin: 0;
}



.collection-item {
    cursor: pointer;
}

.chat-messages {
    border: 1px solid #e0e0e0;
    width: 100%;
    padding: 15px;
    overflow-y: auto;
}

.message {
    display: -webkit-flex;
    display: flex;
}

.message-content {
    border-radius: 3px;
    padding: 6px 9px;
    font-size: 14px;
    margin-bottom: 14px;
    max-width: 250px;
}

.message.owner {
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.message.owner .message-content {
    color: #fff;
    background: lightseagreen;
}
.actions {
    border: 1px solid #e0e0e0;
    height: 30vh;
    padding: 20px;
}
